import {
  type QueryParams,
  type RecQueryParams
} from '../../types/utils'

export const search = (query: QueryParams, jsonObj: any): Array<any> => {
  // If nothing was requested, return the current object
  if (!query.path) return [jsonObj]
  // Tokenize the search terms
  const tokenizeKeys = query.path.split('.')
  // Remove the root token if exists
  if (tokenizeKeys[0] === '$') tokenizeKeys.shift()
  // Call the recursive search
  return searchRec({
    tokenizeKeys,
    results: [],
    ...query
  }, jsonObj)
}

const searchRec = (query: RecQueryParams, jsonObj: any, parent: any): Array<any> => {
  const { valueToMatch, results, limit, tokenizeKeys } = query

  // If jsonObj is undefined or we have enough results we can return
  if (
    typeof jsonObj === 'undefined' ||
    (limit && results.length >= limit)) return results

  // Move the parent pointer
  if (!tokenizeKeys.length) {
    // If we ran out of tokenizeKeys we check the following possiblities:
    // 1. If there is no valueToMatch we can just return the object we are on
    if (valueToMatch === undefined) results.push(jsonObj)
    // 2. If the valueToMatch equals jsonObj, we need to return the parent
    else if (valueToMatch === jsonObj) results.push(parent)
    // 3. If the jsonObj is an array, we need to decend one last time again
    else if (Array.isArray(jsonObj)) {
      jsonObj.forEach(c => searchRec(query, c, jsonObj))
    }
  } else if (Array.isArray(jsonObj)) {
    // Going over each Array child with the same query params
    jsonObj.forEach(c => searchRec(query, c))
  } else if (typeof jsonObj === 'object') {
    // Get a mutable version of tokenizeKeys
    let { tokenizeKeys } = query

    // If it's a recursive token, decent into all children
    // And remove the recursive token from the tokenizeKeys array
    if (tokenizeKeys[0] === '') {
      for (const key in jsonObj) {
        searchRec(query, jsonObj[key])
      }
      tokenizeKeys = tokenizeKeys.slice(1)
    }

    // Decent into child based on the first key in tokenizeKeys
    // With jsonObj as the parent and shifted tokenizeKeys
    const childKey = tokenizeKeys[0]
    tokenizeKeys = tokenizeKeys.slice(1)
    searchRec({ ...query, tokenizeKeys }, jsonObj[childKey], jsonObj)
  }

  return results
}
